* {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
  /* background-color: #212121; */
  background-color: #fff;
  color: black;
  overflow-y: scroll;
  /* color: white; */
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.pagination-ui {
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar {
  height: auto;
  /* box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23); */
  margin: auto;
  z-index: 2;
  width: 100%;
  display: flex;
}

.navbar > * {
  flex: 1;
  align-self: center;
  justify-content: center;
}

.content {
  flex: 1;
  font-size: 20px;
  width: 75%;
  margin: auto;
}

.nav-ul {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-inline-start: 0;
}

.main-chart {
  flex: 1;
}

.nav-li {
  list-style-type: none;
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  flex-direction: column;
}

.nav-li > * {
  cursor: pointer;
}

.nav-li > *:hover {
  opacity: 50%;
}

.link {
  text-decoration: none;
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

a:visited {
  color: black;
}

.link:hover {
  color: grey;
}

.link > * {
  margin: 5px;
}

.nav-icon {
  width: 32px;
  height: 32px;
}

.transaction-item {
  padding-top: 10px;
  padding-bottom: 10px;
  display: flex;
  align-items: center;
}

.transaction-item:not(:last-child) {
  border-bottom: 1px solid lightgrey;
}

.transaction-item > * {
  flex: 1;
}

.add-transaction-form {
  display: flex;
  flex-direction: row;
}

.add-transaction-form > * {
  flex: 1;
}

.add-category-form {
  display: flex;
  flex-direction: row;
  padding-top: 10px;
  padding-bottom: 10px;
}

.add-category-form > * {
  flex: 1;
}


.show-hide-button {
  width: 25px;

  display: flex;
  justify-content: flex-end;
}

.transaction-list {
  min-height: 620px;
}

.transaction-page > * {
  margin-top: 20px;
  margin-bottom: 20px;
}

.transaction-list-header {
  display: flex;
  padding-bottom: 10px;
  display: flex;
  align-items: center;
  /* border-bottom: 1px solid black; */
}

.transaction-list-header > * {
  flex: 1;
  font-weight: bold;
}

.sum-line {
  font-size: 36px;
  font-weight: bold;

  margin-bottom: 10px;
}

.header-text {
  font-size: 36px;
}

.negative-number {
  background: linear-gradient(to right, #f44336, #e91e63, #9c27b0);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: bold;
  width: fit-content;
}

.positive-number {
  background: linear-gradient(to right, #4caf50, #4caf50, #00bcd4);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: bold;
  width: fit-content;
}

.neutral-number {
  background: linear-gradient(to right, #8e24aa, #673ab7, #3f51b5);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: bold;
  width: fit-content;
}

.category-display {
  background: linear-gradient(to right, #2196f3, #03a9f4, #00bcd4);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  font-weight: bold;
  width: fit-content;
}

.transaction-viewer-container {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.transaction-add-container {
  padding: 10px;
}

.content-pane {
  display: flex;
  flex-direction: column;
}

.form-button-container {
  display: flex;
  justify-content: flex-end;
}

.form-button {
  flex: 1;
}

